$appName: mail_roundcube; $cssPrefix: mail_roundcube-;

.loader-container {
  background-image: url('../img/loader.gif');
  background-repeat: no-repeat;
  background-position: center;
  z-index:10;
  width:100%;
  height:100%;
  position:fixed;
  transition: visibility 1s, opacity 1s;
  &.fading {
    opacity: 0;
    visibility: hidden;
  }
}
#errorMsg {
  padding:1em 1em;
  font-weight: bold;
  font-size:120%;
}
